<template>
  <div id="app">
    <button v-on:click="to_main" type="button">main</button>
    <div v-if="status == 'main'">
      <button v-on:click="to_append_driver" type="button">append_driver</button>
      <button v-on:click="to_append_driver_mark" type="button">append_driver_mark</button>
      <button v-on:click="to_list_drivers" type="button">list_drivers</button>
      <button v-on:click="to_find_driver" type="button">find_driver</button>
    </div>
    <div v-else-if="status == 'append_driver'">
      <form onsubmit="return false;">
        <input  type="text" v-model="append_driver_data[0]" placeholder="ПІБ" ref="pib">
        <input type="email" v-model="append_driver_data[1]" placeholder="email">
        <input type="number" v-model="append_driver_data[2]" placeholder="phone">
        <textarea type="text" v-model="append_driver_data[3]" cols="20" rows="5" placeholder="інфо"></textarea>

        <input v-on:click="send_data" type="submit" value="Ok">
      </form>
    </div>
    <div v-else-if="status == 'append_driver_mark'">
      <select v-model="select_driver" > 
        <option value="" disabled selected>Select driver</option>
        <option v-for="item in drivers_arr" :key="item" v-bind:value="item">
          {{item}}
        </option>
        
        
      </select>
      <input type="range" min=0 max=10 step=1 v-model="you_mark">
        {{you_mark}}
      <textarea ref="you_resp" cols="20" rows="5" placeholder="інфо" v-model="you_text"></textarea>
      <input  v-on:click="send_response" type="submit" value="Ok">
    </div>
    <div v-else-if="status == 'list_drivers'">
      <ul v-if="list_drivers">
        <li v-for="item in list_drivers" :key="item">
          {{item.star.toFixed(2)}} star, {{item.pib}}, phone - {{item.phone}}, {{item.email}}, {{item.info}}
        </li>
      </ul>
    </div>
    <div v-else-if="status == 'find_driver'">
       <select v-model="select_driver" > 
        <option value="" disabled selected>Select driver</option>
        <option v-for="item in drivers_arr" :key="item" v-bind:value="item">
          {{item}}
        </option>
      </select>
      <button v-on:click="get_driver_info">get_driver_info</button>
      <p></p>
      <div v-if="one_driver != null">
        {{one_driver.pib}}, {{one_driver.email}}, phone - {{one_driver.phone}}, {{one_driver.info}}
        <ul>
          <li v-for="item in one_driver.marks.length" :key="item">
            {{one_driver.dates[item-1]}}, {{one_driver.marks[item-1]}} star:: {{one_driver.resps[item-1]}}
          </li>
        </ul>
      </div>
    </div>
    <div v-else>
      error!
    </div>
  </div>
</template>
<!--

  status = main, append_driver, append_driver_mark, list_drivers, find_driver
  
 -->
<script>
import axios from 'axios'
function compare( a, b ) {
  if ( a.star > b.star ){
    return -1;
  }
  if ( a.star < b.star ){
    return 1;
  }
  return 0;
}
export default {
  name: 'app',
  data () {
    return {
      server: 'http://localhost:3000/',
      status: "main",
      append_driver_data: Array(4), // дані відправки форми
      drivers_arr: Array(),  // список водіїв для вибору зі списку)
      list_drivers: Array(), // рейтинг
      select_driver: '', // вибраний водій
      you_mark: 5, // оцінка водія
      you_text: '', // ваш відгук
      one_driver: null
    }
  },
  methods:{
    to_main(){
      this.status = "main";
      this.select_driver = '';
      this.you_text = '';
      this.you_mark = 5;
      this.drivers_arr = new Array();
    },
    to_find_driver(){
      axios.get(this.server+'drivers').then((response) => { 
        for(let i=0;i<response.data.length;i++){
          this.drivers_arr.push(response.data[i]); 
        }
      });
      this.status = "find_driver";
    },
    get_driver_info(){
      axios.post(this.server+'find_one_drivers', {
        pib: this.select_driver
      }).then((response) => { 
        this.one_driver = response.data;
      });
    },
    to_append_driver(){
      this.status = "append_driver";
    },
    to_append_driver_mark(){
      axios.get(this.server+'drivers').then((response) => { 
        for(let i=0;i<response.data.length;i++){
          this.drivers_arr.push(response.data[i]); 
        }
      });
      this.status = "append_driver_mark";
    },
    to_list_drivers(){
      axios.get(this.server+'drivers_list').then((response) => { 
        this.list_drivers =  response.data;
        this.list_drivers.sort(compare);
      });
      this.status = "list_drivers";
    },
    send_data(){
      if(this.append_driver_data[0] != null && this.append_driver_data[1] != null && this.append_driver_data[2] != null && this.append_driver_data[3] != null){
        axios.post(this.server+'drivers', {
          pib: this.append_driver_data[0],
          email: this.append_driver_data[1],
          phone: this.append_driver_data[2],
          info: this.append_driver_data[3]
        }).then((response) => {
            console.log("OK");
          }, (error) => {
            console.log(error);
        });  
        this.append_driver_data = new Array(4);
      }
      else{
        this.$refs.pib.focus();
      }
    },
    send_response(){
      if(this.select_driver != '' &&  this.you_text != '' ){
        axios.post(this.server+'drivers_resp', {
          driver: this.select_driver,
          mark: this.you_mark,
          text: this.you_text
        }).then((response) => {
            console.log("OK");
          }, (error) => {
            console.log(error);
        });  
        this.you_text = '';
      }
      else{
        this.$refs.you_resp.focus();
      }
    }
  }
}
</script>

<style>

</style>



